import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { ProgressCircle } from '../';
import { matchTranslationBundle } from '../../utils/UNSAFE_matchTranslationBundle';
import { RootEnvironmentProvider } from '../../UNSAFE_Environment';
import type { RootEnvironment } from '../../UNSAFE_Environment';

describe('Test Progress Circle component', () => {
  it('render', async function () {
    const content = render(<ProgressCircle value={10} />);
    const firstElement = content.container.firstElementChild as HTMLElement;
    const classList = firstElement.classList;
    expect(classList.length).equals(2);
  });

  it('Default value verification using ARIA attributes', async () => {
    const content = render(<ProgressCircle />);
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement.getAttribute('aria-valuemin')).equals('0');
    expect(firstElement.getAttribute('aria-valuemax')).equals('100');
    expect(firstElement.getAttribute('aria-valuenow')).equals('0');
  });

  it('Test for Id ', async () => {
    const content = render(<ProgressCircle id="progressCircle" value={10} />);
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement.getAttribute('id')).equals('progressCircle');
  });

  it('Test default size', async function () {
    const content = render(<ProgressCircle value={10} />);
    const firstElement = content.container.firstElementChild as HTMLElement;
    const classList = firstElement.classList;
    const className = 'oj-c-progress-circle-md';
    expect(classList.contains(className));
  });
});

describe('Test Progress Circle Indeterminate aria-valuetext, translation and accessibleLabel', () => {
  it('Test default aria-valuetext', async function () {
    const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
    const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
    const env: Partial<RootEnvironment> = {
      translations: { '@oracle/oraclejet-preact': translations }
    };
    const content = render(
      <RootEnvironmentProvider environment={env}>
        <ProgressCircle value={'indeterminate'} />
      </RootEnvironmentProvider>
    );
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement.getAttribute('aria-valuetext')).equals('In Progress');
  });

  it('Test accessibleLabel property', async function () {
    const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
    const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
    const env: Partial<RootEnvironment> = {
      translations: { '@oracle/oraclejet-preact': translations }
    };
    const content = render(
      <RootEnvironmentProvider environment={env}>
        <ProgressCircle value={'indeterminate'} accessibleLabel="accessibleLabel" />
      </RootEnvironmentProvider>
    );
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement?.getAttribute('aria-valueText')).equals('accessibleLabel');
  });
});

describe('Test Progress Circle ARIA props ', () => {
  it('Test default aria-valuemin', async () => {
    const content = render(<ProgressCircle value={100} max={1000} />);
    const firstElement = content.container.firstElementChild as HTMLElement;
    expect(firstElement.getAttribute('aria-valuemin')).equals('0');
    expect(firstElement.getAttribute('aria-valuemax')).equals('1000');
    expect(firstElement.getAttribute('aria-valuenow')).equals('100');
  });
});

